            },
            {
                name: '',
                src: 'https://dz-web-job.oss-cn-beijing.aliyuncs.com/%E9%A6%96%E9%A1%B5%E5%B1%95%E7%A4%BA%E8%A7%86%E9%A2%91.mov',
                time: 58000
            },
        ],
        UIcardInner: {
            old : {
              img : require('@/assets/page/old-card-top.png'),
              h4 : ' 2.0  UI',
              p : '',
              button : ' 3.0'
            },
            new : {
              img : require('@/assets/page/new-card-top.png'),
