          },
          {
            title: '-2019',
            img: require('@/assets/page/tyold1.jpg')              
          },
        ]
      }
    },
    mounted() {
      setTimeout(()=>{this.lottiePlay()},1600)
    },
    methods : {
      UIclick(){
        if (this.UIcard  == 'old') {
            this.UIcard  = 'new'
        } else {
            this.movieNum = 0
            this.isMovieOpen = true
        }
      },
      play(){
        this.$refs.video.play()
        this.isPlay = true
        // setTimeout(()=>{this.isPlay = false},this.movieSrc[this.movieNum].time)
      },
      lottiePlay(){
        if(this.$route.path=='/work/cloud-ui-3'){
          setTimeout(()=>{this.$refs.icon0.play()},0)
          setTimeout(()=>{this.$refs.icon1.play()},2000)
          setTimeout(()=>{this.$refs.icon2.play()},4000)
          setTimeout(()=>{this.$refs.icon3.play()},6000)
          setTimeout(()=>{this.$refs.icon0.play()},8000)
          setTimeout(()=>{this.$refs.icon1.play()},8000)
          setTimeout(()=>{this.$refs.icon2.play()},8000)
          setTimeout(()=>{this.$refs.icon3.play()},8000)
          setTimeout(()=>{this.lottiePlay()},11000)
        }
      }
    }
}
